<template>
  <div class="rankMusic">
    <div v-show="bol">
      <div class="block">
        <el-carousel arrow = "never" trigger = "false" height="250px">
          <el-carousel-item v-for="(val,index) in lunboImg" :key="index">
            <img :src = "val.cover"/>
          </el-carousel-item>
        </el-carousel>
      </div>

      <div class="rankDiv">
        <ul>
          <li v-for="(val,index) in icon" :key="index">
            <router-link :to = "val[0].href"><div><img :src="val[0].imgs"/><br/>{{val[0].title}}</div></router-link>
            <router-link :to = "val[1].href"><div><img :src="val[1].imgs"/><br/>{{val[1].title}}</div></router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template> 

<script>  
export default {
  name: 'RankMusic',
  data () {
    return {
      lunboImg:[],
      icon:[
        [
          {imgs:"../../static/imgs/icon1.png",title:"云音乐热歌榜",href:"/home/rankMusic/rankTemplate/1"},
          {imgs:"../../static/imgs/icon2.png",title:"云音乐飙升榜",href:"/home/rankMusic/rankTemplate/3"},
        ],
        [
          {imgs:"../../static/imgs/icon3.png",title:"UK排行榜周榜",href:"/home/rankMusic/rankTemplate/5"},
          {imgs:"../../static/imgs/icon4.png",title:"网易原创歌曲榜",href:"/home/rankMusic/rankTemplate/2"}
        ]
      ],
      bol:true
    }
  },
  beforeCreate(){
    this.$http.get("http://musicapi.leanapp.cn/top/mv",{params:{
      limit : 3
    }}).then((res)=>{
      this.lunboImg = res.body.data;
    },(err)=>{
      console.log(err);
    })
  }
}
</script>

<style scoped>

  a{
    
    text-decoration:none;
    outline: none;
    color: black;
  }

  .rankMusic{
    width: 100%;
    height: 100%;
  }

  img{
    width: 100%;
    height: 100%;
  }

  .rankDiv{
    width: 100%;
    height: calc(100% - 250px);
    display: flex;
    flex-flow: row wrap;
    align-content: center;
    user-select: none;
  }

  .rankDiv ul{
    width: 100%;
    margin: 0;
    padding: 0;
  }

  .rankDiv ul li{
    
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
  }

  .rankDiv ul li div{

    margin: 20px;
    text-align: center;
    cursor: pointer;

  }

  .rankDiv img{
    width: 100px;
    height: 100px;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
</style>



