<template>
  <div class="musicDetails">
      <el-row>
        <el-col style="height:300px ;display: flex;flex-flow: row nowrap; justify-content: center;align-items: center;background:#1e1d23" :span="24">
          <img  :src="mDetails.al.picUrl"/>
        </el-col>
        <el-col :span="24">
          <el-row type="flex" justify="space-between">
            <el-col :span="16">
              <ul style="font-size:14px; color:#b8b8b8; padding-left:20px">
                <li style="font-size:19px; color: black;">{{mDetails.name}}</li>
                <li style="margin-top:10px">发行时间 : {{mDetails.publishTime}}</li>
                <li>歌曲原唱 : <span v-for="(val,index) in mDetails.ar" :key="index">{{val.name}}&nbsp;</span></li>
                <li>音乐风格 : </li>
                <li>所属专辑 : {{mDetails.al.name}}</li>
              </ul>
            </el-col>
            <el-col :span="8" style="padding-right:20px">
              <div class="rd" style="">
                <ul style="color:#b8b8b8;margin:0; padding:0; text-align:center">
                  <li style="">
                    网易云评分
                  </li>
                  <li style="color:black;font-size:20px;font-weight: 600;">
                    6.7分
                  </li>
                  <li style="margin-top:10px;font-size:14px;">
                    {{mDetails.h.size}}人
                  </li>
                </ul>
              </div>
            </el-col>
          </el-row>
        </el-col>

        <el-col :span="24" style="margin-top:20px;padding:0 20px">
          <el-button @click="shopBtn" style="width:100%;letter-spacing:5px;font-size:19px" type="warning" plain>购买</el-button>
        </el-col>
        <el-col :span="24" style="margin-top:40px; padding:0 20px">
          <div style="width:100%;height:1.5px;background:#eae9ef;"></div>
        </el-col>

        <el-col :span = "24" style="font-size:15px;padding:20px 20px">
          <div style="color:#b8b8b8">歌曲鉴赏</div>
          <div style="text-indent: 2em; ">
           {{album.description}}
          </div>
          <div style="margin-top:20px;color:#b8b8b8">
            歌手
          </div>
          <div style="margin:10px 0 0 0;color:#b8b8b8">
            <div style="font-size:12px;text-align:center;display: inline-block;margin:0 10px" v-for="(val,index) in album.artists" :key="index">
              <img style="text-align:center;;width:80px;height:80px" :src="val.picUrl"/><br/>
              {{val.name}}
            </div>
          </div>
        </el-col>

        <el-col style="margin:10px 0;background:#eae9ef; height:50px;line-height: 50px;text-align:center;font-size:19px" :span="24">
          评论
        </el-col>

        <el-col :span="24" style="font-size:15px;padding:0 20px">
          <div style="color:#b8b8b8">
            短评
          </div>
        </el-col>
        <el-col :span="24" style="padding:0 20px;vertical-align: middle;">
          
          <div v-for="(val,index) in commentArr" :key="index" style="margin:10px 0;border-bottom: 1px solid #b8b8b8;">
            <img class="commentImg" style="" :src="val.user.avatarUrl"/>
            <span style="font-weight: 500;">
              {{val.user.nickname}}
            </span>
            <span style="float:right;color:#b8b8b8;font-size:10px;">{{val.time}}</span>
            <br/>
            <div style="padding-bottom:20px;text-indent: 2em;letter-spacing:1px;font-size:14px; color:#b8b8b8;  ">
              {{val.content}}
              <div style="width:100%;">
                <span style="float:right;font-size:12px"><img style="width:15px;height:15px;" src="../../static/imgs/like.png"/>&nbsp;{{val.likedCount}}</span>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
  </div>
</template>

<script>
export default {
  name: 'MusicDetails',
  data () {
    return {
      mDetails:{},
      album:{},
      commentArr:{}
    }
  },
  methods:{
    shopBtn(){
      const that = this;
      console.log(localStorage.username);
      if(localStorage.username == undefined){
        this.$message({
          message: '请先登录账号',
          type: 'warning',
          duration:1500,
          onClose(){
            that.$router.push({
              name:"login"
            });
          }
        });
      }else{
        this.$message({
          message: '购买成功',
          type: 'success',
          duration:1500
        });
      }
    }
  },
  destroyed(){
    console.log("我已经离开了！");
    
  },
  created(to,from,next){
     
      this.$http.get("http://musicapi.leanapp.cn/song/detail",{params:{ids:this.$route.params.musicId}}).then(
          (res) =>{
              this.mDetails = res.body.songs[0];
              this.mDetails.publishTime = getLocalTime(this.mDetails.publishTime);

              this.$http.get("http://musicapi.leanapp.cn/album",{params:{id:this.mDetails.al.id}}).then(
                (res) => {
                  this.album = res.body.album;
                },(err) =>{

                }
              )

              this.$http.get("http://musicapi.leanapp.cn/comment/music",{params:{id:this.$route.params.musicId,limit:100}}).then(
                (res) => {
                  this.commentArr = res.body.hotComments;

                  this.commentArr.forEach((item,index,arr) => {
                    item.time = getLocalTime(item.time)
                  });
                },(err) =>{

                }
              )
          },(err) =>{
            console.log(err);
          }
      )
      console.log();
  }
}

function getLocalTime(dt) {
  var ss = new Date(parseInt(dt)) ;
  return ss.getFullYear()+'年'+(ss.getMonth()+1)+"月"+ss.getDate()+"日";     
}
</script>

<style scoped>
  img{
    
    width: 200px;
    height: 200px;
    
  }

  .musicDetails{
    overflow: auto;
    position: fixed;
    width: 100%;height: 100%;
    background: #f5f4fa;
     
  }
  
  .rd{
    
    display: flex;
    flex-flow: row nowrap; 
    justify-content:center;
    align-items: center;
    float:right;
    margin:14px 0; 
    width:107px;
    height:107px;
    background:rgb(255,255,255);
    word-wrap:break-word ;
    box-shadow: 1px 1px 10px #b8b8b8;
  }

  .commentImg{
    
    width:35px;height:35px;
    border-radius: 50%;
    vertical-align: middle;
  }
</style>



