<template>
  <div class="login">
    <el-row>
      <el-col :span="24" style="text-align:center; font-size:33px; color:#67C23A;margin-bottom:32px">欢迎进入网易云音乐</el-col>
      <div class="inputD" >
        <el-col :span="24" ><el-input style="width:300px" v-model="user.username" clearable type="text" placeholder="手机号 / 邮箱"></el-input></el-col>
        <el-col :span="24" style="margin-bottom:15px"><el-input style="width:300px" v-model="user.password" clearable type="password" placeholder="密码"></el-input></el-col>
        <el-col :span="24"><el-button @click="loginBtn" type="success" style="width:300px; font-weight: bold;">登陆</el-button></el-col>
      </div>
      <el-col style="margin-top:15px;color:#b8b8b8;" :span="24">
        <router-link to = "/register">注册网易</router-link>
        &nbsp;|&nbsp;
        <router-link to = "">忘记密码</router-link>
      </el-col>
      <el-col class="btmC">
        <router-link to = "">微博登陆</router-link>
        &nbsp;|&nbsp;
        <router-link to = "">微信登陆</router-link>
      </el-col>
    </el-row>
    
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      user:{username:"",password:""}
    }
  },
  methods:{
    loginBtn(){
      let that = this;
      if(this.user.username == "" || this.user.password == ""){
        
         this.$message({
            showClose: true,
            message: '警告哦 , 账号或密码为空',
            type: 'warning',
            duration:1500,
            onClose(){
              if(that.user.username == ""){
                document.getElementsByTagName("input")[0].focus();
              }else if(that.user.password == ""){
                document.getElementsByTagName("input")[1].focus();
              }
            }
          });
      }else{
        if(this.user.username == localStorage.username && this.user.password == localStorage.password){
          this.$message({
              showClose: true,
              message: '登陆成功 , 欢迎'+localStorage.name,
              type: 'success',
              duration:1500,
              onClose(){
                that.$router.push("/home/topTabs");
              }    
          });
          
        }else{
          
          this.$message({
              showClose: true,
              message: '警告哦 , 账号或密码错误',
              type: 'warning',
              onClose(){
                
                document.getElementsByTagName("input")[0].focus();
              }
          });
      }
      
      }
      
    }
  },
  mounted(){
    
  }
}
</script>

<style scoped>
  .login{
    position:fixed;
    width: 100%;height: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
  }

  .el-col{
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
  }

  a{
    text-decoration:none;
    outline: none;
    color:#b8b8b8;
  }

  a:focus{
    color:#67C23A;
  }

  .btmC{
    position:fixed;
    bottom: 0px;
    display: flex;
    flex-flow:row nowrap;
    justify-content: space-between;
    padding:0px 60px 20px 60px;
    color: #b8b8b8;
  }
</style>



