<template>
  <div class="searchBox">
      
    <el-input style="width:95%" size="small" @change.native="changeVal" placeholder=" 音乐 / 专辑 / 歌手 / 歌单 " prefix-icon="el-icon-search" v-model="searchVal">
    </el-input>
    
  </div>
</template>

<script>
export default {
  name: 'Search',
  data () {
    return {
        searchVal: ''
    }
  },
  methods:{
    //   gotoSearch(){
    //       console.log("进入")
    //       let that = this;

    //       this.$router.push({
    //           name :"searchNav",
    //           params:{
    //             searchVal:that.searchVal
    //           }
    //       });
    //   },
      changeVal:function(){
          this.$router.push({
              name :"searchNav",
              params:{
                searchVal:this.searchVal
              }
          });
          this.$emit("sousuo",this.searchVal);
      }
  },
  created(){
  }
}
</script>

<style scoped>
    .searchBox{
        width: 100%;
        padding: 5px 0px 10px 0px;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
       
    }

    
</style>



