<template>
  <div class="myInfoLogo">
    <div>
      <img src="../../static/imgs/myIcon.png"/>
      <h1 style="text-align:center;margin:0">wx</h1>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MyInfoLogo',
  data () {
    return {
    }
  }
}
</script>

<style scoped>
  .myInfoLogo{
    position: fixed;
    width: 100%;height: calc(100% - 97px);
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    overflow: auto;
  }

  img{
    width: 100px;height: 100px;
    border-radius: 50%;
  }
</style>



