// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import VueRouter from "vue-router";
import VueResource from "vue-resource";
import ElementUi from "element-ui";
import 'element-ui/lib/theme-chalk/index.css';

import Home from "./components/Home"
import TopTabs from './components/TopTabs'
import RankMusic from './components/RankMusic'
import RankTemplate from './components/RankTemplate'
import MusicDetails from './components/MusicDetails'
import Login from './components/Login'
import Register from './components/Register'
import PlayListAndMv from './components/PlayListAndMv'
import MyInfoLogo from './components/MyInfoLogo'
import SearchNav from './components/SearchNav'








Vue.config.productionTip = false

Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(ElementUi);

const router = new VueRouter({
  routes:[
    {path:"",redirect:"/home",component:Home},
    {path:"/home",redirect:"/home/topTabs",component:Home,
      children:[
        {path:"/home/topTabs",component:TopTabs},
        {path:"/home/rankMusic",component:RankMusic,},
        {path:"/home/rankMusic/rankTemplate/:idx",component:RankTemplate},
        {path:"/home/playListAndMv/:listId",name :"playAndMv",component:PlayListAndMv},
        {path:"/home/myInfoLogo",name : "myInfo",component:MyInfoLogo},
        {path:"/home/searchNav/:searchVal",name : "searchNav",component:SearchNav}
        
      ]
    },
    {path:"/musicDetails/:musicId",name:"musicDetails",component:MusicDetails},
    {path:"/login",name:"login",component:Login},
    {path:"/register",name:"register",component:Register}
  ],
  mode:"history"
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
  render: h => h(App)
})




