<template>
  <div class="searchNav">
      <div class="musicNav">
    <!-- 推荐 -->
        <el-row v-for="(val,index) in valArr" :key="index" type="flex" justify="center" style="border-bottom: 1px solid #b8b8b8;padding:10px 0">
            <el-col :span="18">
            <div class="grid-content bg-purple-dark">
                <!-- 新歌 -->
                <el-row :gutter="10" style="">
                    <el-col :span="10"><img :src = "val.artists[0].img1v1Url"/></el-col>
                    
                    <el-col  :span="14">
                        <div>
                            <div style="font-size:20px; margin-top:10px">{{val.name}}</div>
                            <div style="margin-top: 35px">
                            <ul style="font-size:12px; margin:0;padding:0; color:#b8b8b8;">
                            <li>歌手 : {{val.artists[0].name}}</li>
                            <li>专辑名 : {{val.album.name}}</li>
                            </ul>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </div>
            </el-col>
            <el-col :span="6">
            <div style="position: relative;top:50%;transform: translate(0,-50%); color:#ffafbe; text-align:center" class="grid-content bg-purple-dark">

                <div>
                <div style="vertical-align: middle;font-size:12px"> 
                    播放量  : {{val.duration}}
                </div>
                <el-button @click="pushId(val.id)"  style="margin-top:10px;border-color:#ffafbe; color:#ffafbe" size="mini">详情</el-button>
                </div>
                
            </div>
            </el-col>
        </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchNav',
  data () {
    return {
      valArr:{}
    }
  },
  methods:{
    pushId(musicId){
      this.$router.push({
        name:"musicDetails",
        params:{
          musicId
        }
      })
    }
  },
  created(){
      this.$http.get("http://musicapi.leanapp.cn/search",{params:{
        keywords :this.$route.params.searchVal
        }}).then(
            (res) => {
                console.log(res);
                this.valArr = res.body.result.songs;
                this.valArr.forEach((item,index,arr) => {
                    if(item.name.length > 8) {
                      item.name = item.name.substr(0,8)+"...";
                    } 
                    item.album.publishTime = getLocalTime(item.album.publishTime);
                });
            },
            (err) => {
                console.log(err);
        })
  }
}

function getLocalTime(dt) {
    var ss = new Date(parseInt(dt)) ;
    return ss.getFullYear()+'/'+(ss.getMonth()+1)+"/"+ss.getDate();
}
</script>

<style scoped>
  div{
    overflow: scroll;
    overflow-x: hidden;
    
  }

  img{
    padding:0px 10px;
    width: 100px;
    height: 152px;
    vertical-align: top;
  }

  .musicNav{
    position: fixed;
    height: calc(100% - 97px);
    width: 100%;
    overflow: auto;
  }
</style>



