<template>
  <div class="topTabs">
      <el-tabs  style="border:0px" :stretch = "bol" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane :lazy = "true" label="推荐音乐" name="first"><MusicNav httpVal = "personalized/newsong"/></el-tab-pane>
            <el-tab-pane :lazy = "true" label="推荐歌单" name="second"><MusicNav httpVal = "personalized/"/></el-tab-pane>
            <el-tab-pane :lazy = "true" label="推荐MV" name="third"><MusicNav httpVal = "personalized/mv"/></el-tab-pane>
       </el-tabs>
  </div>
</template>

<script>
import MusicNav from "./MusicNav";



export default {
  name: 'TopTabs',
  data () {
    return {
        activeName: 'first',
        bol:true,
        topTitle:[],
    }
  },
  methods: {
    handleClick(tab, event) {
    }  
  },
  components:{
      MusicNav
  }
}
</script>

<style scoped>
    .topTabs{
        letter-spacing: 2px;
    }

   

    .el-tabs__header{
        margin: 0px;
    }
</style>



