<template>
  <div class="register">
    <el-row>
      <el-col :span="24" style="text-align:center; font-size:33px; color:#67C23A;margin-bottom:32px">欢迎进入网易云音乐</el-col>
      <div class="inputD" >
        <el-col :span="24" ><el-input style="width:300px" v-model="user.username" clearable type="text" placeholder="手机号 / 邮箱"></el-input></el-col>
        <el-col :span="24" ><el-input style="width:300px" v-model="user.password" clearable type="password" placeholder="密码(最少六位)"></el-input></el-col>
        <el-col :span="24" style="margin-bottom:15px"><el-input style="width:300px" v-model="user.name" clearable type="text" placeholder="昵称"></el-input></el-col>
        <el-col :span="24"><el-button  @click="registerBtn" type="success" style="width:300px; font-weight: bold;">注册</el-button></el-col>
      </div>
      <el-col style="margin-top:15px;color:#b8b8b8; font-size:13px" :span="24">
        点击「下一步」代表你已阅读并同意<router-link to ="">用户使用协议</router-link>
      </el-col>
      
    </el-row>
    
  </div>
</template>

<script>
export default {
  name: 'Register',
  data () {
    return {
      user:{username:"",password:"",name:""}
    }
  },
  methods:{
    registerBtn(){
      let reg = /^1[34578][0-9]{9}/;
      let reg1 = /[a-zA-Z0-9]{1,10}@[a-zA-Z0-9]{1,5}\.[a-zA-Z0-9]{1,5}/;
      let that = this;

      if(this.user.username == "" || this.user.password == ""){
        
         this.$message({
            showClose: true,
            message: '警告哦 , 请输入信息',
            type: 'warning',
            duration:1500,
            onClose(){
              if(that.user.username == ""){
                document.getElementsByTagName("input")[0].focus();
              }else if(that.user.password == ""){
                document.getElementsByTagName("input")[1].focus();
              }else if(that.user.name == ""){
                document.getElementsByTagName("input")[2].focus();
              }
            }
          });
      }else{
        if(reg.test(this.user.username)||reg1.test(this.user.username)&& this.user.password.length >= 6){
          localStorage.setItem("username",this.user.username);
          localStorage.setItem("password",this.user.password);
          localStorage.setItem("name",this.user.name);

          
          this.$message({
            showClose: true,
              message: '注册成功'+that.user.name+' , 跳转........',
              type: 'success',
              duration:1500,
              onClose(){
                that.$router.push("/login");  
              }    
          });
          
        }else{
          this.$message({
            showClose: true,
              message: '警告哦 , 账号或密码格式不对',
              type: 'warning',
              onClose(){
                
                document.getElementsByTagName("input")[0].focus();   
              }    
          });
      }
      }
      
      
    }
  }
}
</script>

<style scoped>
  .register{
    position:fixed;
    width: 100%;height: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
  }

  .el-col{
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
  }

  a{
    outline: none;
    color:#b8b8b8;
  }

  a:focus{
    color:#67C23A;
  }

</style>



