<template>
  <div  class="btmRouterTabs">
      
      <div class="navDiv"><router-view></router-view></div>
      
      <div class="btdiv">
        <router-link  v-for="(val,index) in imgArr"  :key="index" :to="val.routerHttp">
            <div class="tabDiv" @click="imgArrClick(index)">
                <img v-show="val.bol" :src="val.imgs[0]"/>
                <img v-show="!val.bol" :src="val.imgs[1]"/>
                <div v-show="val.bol" style="text-align:center;color:#b8b8b8"> {{val.name}} </div>
                <div v-show="!val.bol" style="text-align:center;color:black"> {{val.name}}  </div>
            </div>
            
        </router-link>
        
      </div>
  </div>
</template>

<script>

export default {
  name: 'BtmRouterTabs',
  data () {
    return {
        imgArr:[
            {routerHttp:"/home/topTabs",imgs:["../../static/imgs/tuijian_2.png","../../static/imgs/tuijian_1.png"],bol:false,name:"推荐"},
            {routerHttp:"/home/rankMusic",imgs:["../../static/imgs/rank1.png","../../static/imgs/rank2.png"],bol:true,name:"排行榜"},
            {routerHttp:"/home/myInfoLogo",imgs:["../../static/imgs/personainfo1.png","../../static/imgs/personainfo.png"],bol:true,name:"我的"},
        ],
        aColor1:"aColor1",
        aColor2:"aColor2"
    }
  },
  methods:{
      imgArrClick(index){
          this.imgArr.forEach((item,index,arr) =>{
              item.bol = true;
          })
          this.imgArr[index].bol = !this.imgArr[index].bol;
      }
  },
  created(){
      console.log("主页面"+this.$route.path);
      this.imgArr.forEach((item,index,arr) =>{
         item.routerHttp ==  this.$route.path ? this.imgArrClick(index) : console.log("无事发生");
      })  

      

  }
}


</script>

<style scoped>
    .btmRouterTabs{
        position: relative;
        height: calc(100% - 47px);
        display: flex;
        flex-flow: column nowrap;
        
    }

    .btdiv{
        
        position: fixed;
        bottom: 0px;
        height: 50px;
        width: 100%;
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-around;
        align-items: center;
    }

    img{
        width: 30px;
        vertical-align: bottom;
    }

    .navDiv{
        position: fixed;
        height: calc(100% - 97px);
        width: 100%;
    }

    a{
        text-decoration:none;
        outline: none;
        color: black;
    }

    .aColor1{color:"black"}
    .aColor2{color:"#ffafbe"}
   

    .tabDiv{
        font-size: 8px;
    }
</style>



