<template>
  <div class="musicNav">
    <!-- 推荐 -->
      <el-row v-if = "httpVal == 'personalized/newsong'" v-for="(val,index) in musicArr" :key="index" type="flex" justify="center" style="border-bottom: 1px solid #b8b8b8;padding:10px 0">
        <el-col :span="18">
          <div class="grid-content bg-purple-dark">
            <!-- 新歌 -->
            <el-row  :gutter="10" style="">
              <el-col :span="10"><img :src = "val.picUrl"/></el-col>
              
              <el-col  :span="14">
                <div>
                    <div style="font-size:20px; margin-top:10px">{{val.name}}</div>
                    <div style="margin-top: 35px">
                      <ul style="font-size:12px; margin:0;padding:0; color:#b8b8b8;">
                      <li>歌手 : {{val.song.artists[0].name}}</li>
                      <li>专辑名 : {{val.song.album.name}}</li>
                      <li>出版公司 : {{val.song.album.company}}</li>
                    </ul>
                    </div>
                </div>
              </el-col>
            </el-row>
          </div>
        </el-col>
        <el-col :span="6">
          <div style="position: relative;top:50%;transform: translate(0,-50%); color:#ffafbe; text-align:center" class="grid-content bg-purple-dark">

            <div>
              <div style="vertical-align: middle;font-size:12px">
                播放量  : {{index}}
              </div>
              <router-link :to = "{path:'/musicDetails/'+val.id}">
                <el-button style="margin-top:10px;border-color:#ffafbe; color:#ffafbe" size="mini">详情</el-button>
              </router-link>
            </div>
            
          </div>
        </el-col>
      </el-row>
<!-- 歌单 -->
      <el-row v-if = "httpVal == 'personalized/'" v-for="(val,index) in musicArr" :key="index" type="flex" justify="center" style="border-bottom: 1px solid #b8b8b8;padding:10px 0">
        <el-col :span="18">
          <div class="grid-content bg-purple-dark">
            <el-row  :gutter="10" style="">
              <el-col :span="10"><img :src = "val.picUrl"/></el-col>
              
              <el-col  :span="14">
                <div>
                    <div style="font-size:20px; margin-top:10px">{{val.name}}</div>
                    <div style="margin-top: 35px">
                      <ul style="font-size:12px; margin:0;padding:0; color:#b8b8b8;">
                        <li>推荐 : {{val.copywriter}}</li>
                      </ul>
                    </div>
                </div>
              </el-col>
            </el-row>
          </div>
        </el-col>
        <el-col :span="6">
          <div style="position: relative;top:50%;transform: translate(0,-50%); color:#ffafbe; text-align:center" class="grid-content bg-purple-dark">

            <div>
              <div style="vertical-align: middle;font-size:12px">
                播放量  : {{index}}
              </div>
                <el-button @click="playList(val.id)" style="margin-top:10px;border-color:#ffafbe; color:#ffafbe;font-size:12px" size="mini">歌单详情</el-button>
            </div>
            
          </div>
        </el-col>
      </el-row>
<!-- mv -->
      <el-row v-if = "httpVal == 'personalized/mv'" v-for="(val,index) in musicArr" :key="index" type="flex" justify="center" style="border-bottom: 1px solid #b8b8b8;padding:10px 0">
        <el-col :span="18">
          <div class="grid-content bg-purple-dark">
            <el-row v-if = "httpVal == 'personalized/mv'" :gutter="10" style="">
              <el-col :span="10"><img :src = "val.picUrl"/></el-col>
              <el-col  :span="14">
                <div>
                    <div style="font-size:20px; margin-top:10px">{{val.name}}</div>
                    <div style="margin-top: 35px">
                      <ul style="font-size:12px; margin:0;padding:0; color:#b8b8b8;">
                      <li>歌手 : {{val.artists[0].name}}</li>
                    </ul>
                    </div>
                </div>
              </el-col>
            </el-row>
          </div>
        </el-col>
        <el-col :span="6">
          <div style="position: relative;top:50%;transform: translate(0,-50%); color:#ffafbe; text-align:center" class="grid-content bg-purple-dark">

            <div>
              <div style="vertical-align: middle;font-size:12px">
                播放量  : {{(val.playCount)}} 万
              </div>
              <router-link :to = "{path:'/musicDetails/'+val.id}">
                <el-button style="margin-top:10px;border-color:#ffafbe; color:#ffafbe" size="mini">购买</el-button>
              </router-link>
            </div>
            
          </div>
        </el-col>
      </el-row>
  </div>
</template>

<script>
import altImg from '../assets/89.png';


export default {
  name: 'MusicNav',
  data () {
    return {
      musicArr:"",
      nav:".ndv",
      altImg
    }
  },
  props:{
    httpVal:{
      type:String,
    }
  },
  methods:{
     playList(listId){
       this.$router.push({
          name :"playAndMv",
          params:{
            listId
          }
       })
     }
  },
  created(){
    
    this.$http.get("http://musicapi.leanapp.cn/"+this.httpVal).then(function(res){
      
      if(this.httpVal == "personalized/newsong"){
        this.musicArr = res.body.result;
        this.musicArr.forEach((item,index,arr) => {
            (item.picUrl == null) ? this.musicArr[index].picUrl = this.altImg : console.log("true");
        });
      }
      
      if(this.httpVal == "personalized/"){
        this.musicArr = res.body.result;
        this.musicArr.forEach((item,index,arr) => {
            (item.picUrl == null) ? this.musicArr[index].picUrl = this.altImg : console.log("true");
        });
      }

      if(this.httpVal == "personalized/mv"){
        this.musicArr = res.body.result;
        this.musicArr.forEach((item,index,arr) => {
            
            (item.picUrl == null) ? this.musicArr[index].picUrl = this.altImg : console.log("true");
            this.musicArr[index].playCount = (this.musicArr[index].playCount/10000).toFixed(2);
        });
      }
    },function(err){
      console.log(err);
    })
  }
}
</script>
 
<style scoped>
  img{
    padding:0px 10px;
    width: 100px;
    height: 152px;
    vertical-align: top;
  }

  .musicNav{
    position: fixed;
    height: calc(100% - 137px);
    width: 100%;
    overflow: auto;
  }
</style>



