<template>
  <div class="home">
      <Search @sousuo = "getSerachVal"/>
      <BtmRouterTabs/>
  </div>
</template>

<script>
import Search from './Search'
import BtmRouterTabs from './BtmRouterTabs'
export default {
  name: 'Home',
  data () {
    return {
        serachVal:""
    }
  },
  methods:{
      getSerachVal(val){
          this.serachVal = val;
          console.log(val);
      }
  },
  components:{
      Search,
      BtmRouterTabs
  }
}

window.onresize = function(){
  const html = document.getElementsByTagName("html");
  console.log(html.offsetHeight);
  html[0].style.height = (window.innerHeight)+"px";
  html[0].style.width = (window.innerWidth)+"px";
}

window.onresize();
</script>

<style scoped>
   .home{
       position: fixed;
       width: 100%;
       height: 100%;
       overflow: scroll;
       overflow-x: hidden;
       overflow-y: hidden;
   }
</style>



