<template>
  <div>
  <div>
    <el-row  style="font-size:15px ;text-align:center;height:40px;border-bottom: 1px solid black;">
        <el-col :span="24">
            {{musicArr.name}}
        </el-col>
      </el-row>
  </div>
  <div class="musicNav">
    <!-- 推荐 -->
      
      <el-row v-for="(val,index) in musicArr.tracks" :key="index" type="flex" justify="center" style="border-bottom: 1px solid #b8b8b8;padding:10px 0">
        <el-col :span="18">
          <div class="grid-content bg-purple-dark">
            <!-- 新歌 -->
            <el-row  :gutter="10" style="">
              <el-col :span="10"><img :src = "val.al.picUrl"/></el-col>
              
              <el-col  :span="14">
                <div>
                    <div style="font-size:20px; margin-top:10px">{{val.name}}</div>
                    <div style="margin-top: 35px">
                      <ul style="font-size:12px; margin:0;padding:0; color:#b8b8b8;">
                      <li>歌手 : {{val.ar[0].name}}</li>
                      <li>专辑名 : {{val.al.name}}</li>
                    </ul>
                    </div>
                </div>
              </el-col>
            </el-row>
          </div>
        </el-col>
        <el-col :span="6">
          <div style="position: relative;top:50%;transform: translate(0,-50%); color:#ffafbe; text-align:center" class="grid-content bg-purple-dark">

            <div>
              <div style="vertical-align: middle;font-size:12px">
                播放量  : {{index}}
              </div>
              <el-button @click="pushId(val.id)"  style="margin-top:10px;border-color:#ffafbe; color:#ffafbe" size="mini">购买</el-button>
            </div>
            
          </div>
        </el-col>
      </el-row>
  </div>
  </div>
</template>

<script>
import altImg from '../assets/89.png';


export default {
  name: 'PlayListAndMv',
  data () {
    return {
      musicArr:{}
    }
  },
  methods:{
    pushId(musicId){
      this.$router.push({
        name:"musicDetails",
        params:{
          musicId
        }
      })
    }
  },
  created(){

    console.log();

    if(this.$route.params.listId != null){
      this.$http.get("http://musicapi.leanapp.cn/playlist/detail",{params:{id:this.$route.params.listId}}).then(function(res){

      this.musicArr = res.body.playlist;
        
      },function(err){
        console.log(err);
      })
    }
   
  }
}
</script>
 
<style scoped>
  div{
    overflow: scroll;
    overflow-x: hidden;
    
  }

  img{
    padding:0px 10px;
    width: 100px;
    height: 152px;
    vertical-align: top;
  }

  .musicNav{
    position: fixed;
    height: calc(100% - 137px);
    width: 100%;
    overflow: auto;
  }
</style>



